@import "./../../Common/variables";
.contactsBlock {

  /*border: 1px solid black;*/
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  box-sizing: border-box;
  background-color: $containerColor;
  border-radius: 4px;
  >div{
    width: 100%;
    margin-bottom: 10px;
  }

  @media screen and (max-width: 500px) {

    padding-top: 1rem;
    margin-left: 1.875rem;
    display: flex;
    flex-direction: column !important;
    flex-wrap: wrap !important;
    font-size: 17px;
  }
  @media screen and (max-width: 1620px) {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-left: 2rem;
    padding-right: 1rem;

  }

  .contactsBlockItem {
    display: flex;
    align-items: center;
    margin-bottom: 0.6875rem;
    flex-wrap: wrap;
    margin-right: 1rem;

    .icon {
      margin-right: 0.875rem;
    }

    a {
      color: #44566c;
      cursor: pointer;
      text-decoration: none;
    }
  }

  .lastContactBlockItem {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-right: 1rem;
    @media screen and (max-width: 1620px) {
      margin-bottom: 0.6875rem;
    }

    .icon {
      margin-right: 0.875rem;
    }

    a {
      color: #44566c;
      cursor: pointer;
      text-decoration: none !important;
    }
  }
}








